'use server';

import { z } from 'zod';

const contactFormSchema = z.object({
  name: z.string().min(2, { message: "Name must be at least 2 characters." }),
  email: z.string().email({ message: "Please enter a valid email address." }),
  message: z.string().min(10, { message: "Message must be at least 10 characters." }),
});

export interface ContactFormState {
  message: string;
  errors?: {
    name?: string[];
    email?: string[];
    message?: string[];
    _form?: string[];
  };
  success: boolean;
}

export async function submitContactForm(
  prevState: ContactFormState,
  formData: FormData
): Promise<ContactFormState> {
  const validatedFields = contactFormSchema.safeParse({
    name: formData.get('name'),
    email: formData.get('email'),
    message: formData.get('message'),
  });

  if (!validatedFields.success) {
    return {
      errors: validatedFields.error.flatten().fieldErrors,
      message: "Validation Error: Please check your input.",
      success: false,
    };
  }

  // In a real application, you would send an email or save to a database here.
  // For example: await sendEmail(validatedFields.data);
  console.log("Contact Form Submitted:", validatedFields.data);

  // Simulate a delay for an API call
  await new Promise(resolve => setTimeout(resolve, 1000));

  // Example of a potential submission error from the backend
  // if (Math.random() > 0.8) { // Simulate a random error
  //   return {
  //     message: "Server Error: Could not send message. Please try again later.",
  //     success: false,
  //     errors: { _form: ["Server Error: Could not send message. Please try again later."] }
  //   };
  // }

  return {
    message: `Thank you, ${validatedFields.data.name}! Your message has been received.`,
    success: true,
  };
}
