"use client";

import { useActionState, useEffect } from 'react';
import { useFormStatus } from 'react-dom';
import { submitContactForm, type ContactFormState } from '@/app/actions';
import { SOCIAL_LINKS_DATA } from '@/lib/constants';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Label } from '@/components/ui/label';
import Link from 'next/link';
import { useToast } from '@/hooks/use-toast';
import { Loader2 } from 'lucide-react';

function SubmitButton() {
  const { pending } = useFormStatus();
  return (
    <Button type="submit" disabled={pending} size="lg" className="w-full md:w-auto">
      {pending ? (
        <>
          <Loader2 className="mr-2 h-4 w-4 animate-spin" />
          Sending...
        </>
      ) : (
        'Send Message'
      )}
    </Button>
  );
}

export default function ContactSection() {
  const initialState: ContactFormState = { message: '', success: false };
  const [state, formAction] = useActionState(submitContactForm, initialState);
  const { toast } = useToast();

  useEffect(() => {
    if (state.message) {
      if (state.success) {
        toast({
          title: "Success!",
          description: state.message,
          variant: 'default', // Shadcn default is not 'success' explicitly
        });
        // Optionally reset form fields here if not using react-hook-form reset
        const form = document.getElementById('contact-form') as HTMLFormElement;
        form?.reset();

      } else {
        // Display form-level errors or specific field errors if available
        const errorMessage = state.errors?._form?.[0] || state.message || "An error occurred.";
        toast({
          title: "Error",
          description: errorMessage,
          variant: "destructive",
        });
      }
    }
  }, [state, toast]);


  return (
    <section id="contact" className="py-16 md:py-24 bg-secondary/30">
      <div className="container mx-auto px-4">
        <h2 className="text-3xl md:text-4xl font-bold text-center mb-6">
          Get In <span className="text-primary">Touch</span>
        </h2>
        <p className="text-center text-lg text-muted-foreground mb-12 max-w-2xl mx-auto">
          Have a project in mind, a question, or just want to say hi? Feel free to reach out!
        </p>

        <div className="grid md:grid-cols-2 gap-12 items-start">
          <form id="contact-form" action={formAction} className="space-y-6 p-6 md:p-8 bg-card rounded-lg shadow-lg">
            <div>
              <Label htmlFor="name" className="text-base">Full Name</Label>
              <Input type="text" id="name" name="name" required className="mt-2 text-base" />
              {state.errors?.name && <p className="text-sm text-destructive mt-1">{state.errors.name.join(', ')}</p>}
            </div>
            <div>
              <Label htmlFor="email" className="text-base">Email Address</Label>
              <Input type="email" id="email" name="email" required className="mt-2 text-base" />
              {state.errors?.email && <p className="text-sm text-destructive mt-1">{state.errors.email.join(', ')}</p>}
            </div>
            <div>
              <Label htmlFor="message" className="text-base">Message</Label>
              <Textarea id="message" name="message" rows={5} required className="mt-2 text-base" />
              {state.errors?.message && <p className="text-sm text-destructive mt-1">{state.errors.message.join(', ')}</p>}
            </div>
            <SubmitButton />
          </form>

          <div className="space-y-6 p-6 md:p-8">
            <h3 className="text-2xl font-semibold mb-4 text-primary">Connect with Me</h3>
            <p className="text-muted-foreground text-base">
              I'm active on several platforms. Follow me or send a direct message!
            </p>
            <div className="flex flex-wrap gap-4">
              {SOCIAL_LINKS_DATA.map(link => (
                <Button key={link.name} variant="outline" size="icon" asChild className="transition-colors hover:border-primary hover:text-primary">
                  <Link href={link.url} target="_blank" rel="noopener noreferrer" aria-label={`Connect on ${link.name}`}>
                    <link.Icon className="h-5 w-5" />
                  </Link>
                </Button>
              ))}
            </div>
             <div className="mt-8 pt-6 border-t">
              <h4 className="text-xl font-semibold mb-2">Contact Information</h4>
              <p className="text-muted-foreground">
                <strong>Email:</strong> your.email@example.com <br />
                <strong>Location:</strong> Your City, Your Country
              </p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
